import React, { useState, useCallback } from "react";
import {
    Grid,
    Typography,
    List,
    ListItem,
    ListItemText,
    ListItemSecondaryAction,
    Checkbox,
    Icon,
    IconButton,
    TextField,
    Select,
    MenuItem,
    Button,
    FormControlLabel,
    } from "@mui/material/";
import { useLocation } from 'react-router-dom';
import update from "immutability-helper";
import MammItem from "./ConstructReviewComponents/MammItem";

function ConstructReviewPage ({}) {

    let location = useLocation();
    let data = location.state
    let constructs = data.ConstructList
    let construct_indices = constructs.map((_,index) => index)
    let bacterial_indices = construct_indices.filter((index) => constructs[index].vector_info.vector_system==="Bacterial")
    let insect_indices = construct_indices.filter((index) => constructs[index].vector_info.vector_system==="Insect")
    let mammalian_indices = construct_indices.filter((index) => constructs[index].vector_info.vector_system==="Mammalian")
    let special_indices = construct_indices.filter((index) => constructs[index].vector_info.vector_system==="All")

    const [bacterialChecks, setBacterialChecks] = useState(bacterial_indices.map((ind) => [true, true, ["BL21(DE3)"]]))
    const [insectChecks, setInsectChecks] = useState(insect_indices.map((ind) => [true, true, true]))
    const [mammalianAmounts, setMammalianAmounts] = useState(mammalian_indices.map(()=> 1))


    const handleBacStrainSelector = (index) => (event) => {
        let temp_value = ((bacterialChecks[index][0]||bacterialChecks[index][1])&&event.target.value.length===0?["BL21(DE3)"]:event.target.value)
        let temp = update(bacterialChecks, {[index]: {2: {$set: temp_value}}})
        setBacterialChecks(temp)
    }

    const handleSDSEval = (index, state, setter) => (event) => {
        let temp = update(state, {[index]: {0: {$set: event.target.checked}}})
        setter(temp)
    }

    const handleWesternEval = (index, state, setter) => (event) => {
        let temp = update(state, {[index]: {1: {$set: event.target.checked}}})
        setter(temp)
    }

    const handleGenerateVirus = (index, state, setter) => (event) => {
        let temp = update(state, {[index]: {2: {$set: event.target.checked}}})
        setter(temp)
    }

    //TODO: add input for reference project number

   //TODO: add select for co-express with

    const BacItem = ({con_index, bac_index, bacChecks}) => {

        let construct = constructs[con_index]
        let construct_name_site1 = (construct.Site1!==null?construct.Site1.map((subelement) => subelement.element_name).join("-"):"")
        let construct_name_site2 = (construct.Site2!==null?construct.Site2.map((subelement) => subelement.element_name).join("-"):"")
        let isDuet = construct.vector_info.mcs2!==null
        let construct_name = ""
        if (isDuet) {
            construct_name = ((construct_name_site1.length>0?construct_name_site1+" in site 1; ":"")+(construct_name_site2.length>0?construct_name_site2+" in site 2":""))
        } else {
            construct_name = construct_name_site1
        }


        return <ListItem fullWidth>
            <ListItemText
                primary={construct_name}
                secondary={"in "+construct.vector_info.vector_name}/>
            <ListItemSecondaryAction>
                <FormControlLabel control={<Checkbox checked={bacChecks[0]} onChange={handleSDSEval(bac_index, bacterialChecks, setBacterialChecks)} />} label="SDS-PAGE"/>
                <FormControlLabel control={<Checkbox checked={bacChecks[1]} onChange={handleWesternEval(bac_index, bacterialChecks, setBacterialChecks)} />} label="Western"/>
                <Select
                    disabled={!(bacChecks[0]||bacChecks[1])}
                    multiple
                    value={bacChecks[2]}
                    onChange={handleBacStrainSelector(bac_index)}>
                    <MenuItem value={"BL21(DE3)"}>BL21(DE3)</MenuItem>
                    <MenuItem value={"SHUFFLE"}>SHUFFLE</MenuItem>
                </Select>
            </ListItemSecondaryAction>
        </ListItem>
    }

    const InsectItem = ({con_index, ins_index, insChecks}) => {

        let construct = constructs[con_index]
        let construct_name_site1 = (construct.Site1!==null?construct.Site1.map((subelement) => subelement.element_name).join("-"):"")
        let construct_name_site2 = (construct.Site2!==null?construct.Site2.map((subelement) => subelement.element_name).join("-"):"")
        let isDuet = construct.vector_info.mcs2!==null
        let construct_name = ""
        if (isDuet) {
            construct_name = ((construct_name_site1.length>0?construct_name_site1+" in site 1; ":"")+(construct_name_site2.length>0?construct_name_site2+" in site 2":""))
        } else {
            construct_name = construct_name_site1
        }


        return <ListItem fullWidth>
            <ListItemText
                primary={construct_name}
                secondary={"in "+construct.vector_info.vector_name}/>
            <ListItemSecondaryAction>
                <FormControlLabel control={<Checkbox checked={insChecks[2]} onChange={handleGenerateVirus(ins_index, insectChecks, setInsectChecks)} />} label="Generate Virus"/>
                <FormControlLabel control={<Checkbox checked={insChecks[0]} onChange={handleSDSEval(ins_index, insectChecks, setInsectChecks)} />} label="SDS-PAGE"/>
                <FormControlLabel control={<Checkbox checked={insChecks[1]} onChange={handleWesternEval(ins_index, insectChecks, setInsectChecks)} />} label="Western"/>
            </ListItemSecondaryAction>
        </ListItem>
    }


     //TODO: add 'select all' for each check box sub section
     //TODO: at end, make pretty

    return (
        <div>
            <Grid container fullWidth sx={{width:"100%"}} justifyContent={"center"}>
                <Grid container justifyContent={"center"} sx={{mt:2}}>
                    <Grid item>
                        <Typography>Review Construct Options1</Typography>
                    </Grid>
                </Grid>
                <Grid container fullWidth sx={{width:"100%"}} justifyContent={"center"}>
                <List dense={true} fullWidth sx={{width:"50%"}}>
                    {bacterial_indices.map((con_index, index)=><BacItem con_index={con_index} bac_index={index} bacChecks={bacterialChecks[index]}/>)}
                </List>
                </Grid>
                <Grid container fullWidth sx={{width:"100%"}} justifyContent={"center"}>
                <List dense={true} fullWidth sx={{width:"50%"}}>
                    {insect_indices.map((con_index, index)=><InsectItem con_index={con_index} ins_index={index} insChecks={insectChecks[index]}/>)}
                </List>
                </Grid>
                <Grid container fullWidth sx={{width:"100%"}} justifyContent={"center"}>
                <List dense={true} fullWidth sx={{width:"50%"}}>
                    {insect_indices.map((con_index, index)=><MammItem constr={constructs[con_index]} mam_index={index} mammalianAmounts={mammalianAmounts} setMammalianAmounts={setMammalianAmounts}/>)}
                </List>
                </Grid>
            </Grid>
            <Grid container fullWidth sx={{width:"100%"}} justifyContent={"center"}>
                <Button
                    variant={'contained'}
                    onClick={() => {
                        console.log("CHECK BUTTON START")
                        console.log("constructs", constructs)
                        console.log("bac constructs", bacterial_indices.map((ind) =>constructs[ind]))
                        console.log("mamState", mammalianAmounts)
                        console.log("CHECK BUTTON END")
                        return }}>
                    Debugger
                </Button>
            </Grid>
        </div>
    )
}

export default ConstructReviewPage
